'use client';
import { useState, useEffect } from 'react';
import ProductList from '../components/ProductList';
import Cart from '../components/Cart';

const API_PRODUCTS_URL = 'http://oms-backend.test/api/products';
const API_ORDERS_URL = 'http://oms-backend.test/api/orders';

export default function Home() {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [orderNumber, setOrderNumber] = useState(null);
  const [orderStatus, setOrderStatus] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch products from backend
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        setLoading(true);
        const response = await fetch(API_PRODUCTS_URL);
        if (!response.ok) throw new Error('Failed to fetch products');
        const data = await response.json();
        setProducts(data);
      } catch (err) {
        console.error('Error fetching products:', err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, []);

  // Add item to cart
  const addToCart = (product, quantity) => {
    setCart((prevCart) => {
      const existingItem = prevCart.find(item => item.product_id === product.id);
      if (existingItem) {
        return prevCart.map(item =>
          item.product_id === product.id
            ? { ...item, quantity: item.quantity + quantity }
            : item
        );
      } else {
        return [
          ...prevCart,
          { product_name: product.name, product_id: product.id, unit_price: parseFloat(product.price), quantity }
        ];
      }
    });
  };

  // Update quantity or remove item
  const updateCartItem = (productId, quantity) => {
    if (quantity <= 0) {
      setCart(cart.filter(item => item.product_id !== productId));
    } else {
      setCart(cart.map(item =>
        item.product_id === productId ? { ...item, quantity } : item
      ));
    }
  };

  const calculateTotal = () =>
    cart.reduce((total, item) => total + item.unit_price * item.quantity, 0);

 // Create order
const createOrder = async () => {
  if (cart.length === 0) return alert('Cart is empty');

  const payload = {
    total_amount: calculateTotal(),
    status: 'created',
    items: cart,
  };

  try {
    const response = await fetch(API_ORDERS_URL, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload),
    });

    if (!response.ok) throw new Error('Failed to create order');

    const data = await response.json();
    // assume backend returns something like { order_number: 'ORD-20251112-0001' }
    setOrderNumber(data.data.order_number);
    setOrderStatus('created');

    alert(`Order created successfully!`);
  } catch (error) {
    console.error('Error creating order:', error);
    alert(`Error: ${error.message}`);
  }
};

// Confirm checkout
const checkout = async () => {
  console.log(orderNumber);
  if (!orderNumber) return alert('Please create an order first');

  const payload = {
    order_number: orderNumber,
    total_amount: calculateTotal(),
    status: 'confirmed',
    items: cart,
  };

  try {
    const response = await fetch(API_ORDERS_URL, {
      method: 'POST', // âœ… use POST for update/confirm
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload),
    });

    if (!response.ok) throw new Error('Failed to confirm order');
    setOrderStatus('confirmed');
    alert('Order confirmed!');
  } catch (error) {
    console.error('Error confirming order:', error);
    alert(`Error: ${error.message}`);
  }
};


  // Cancel order
  const cancelOrder = async () => {
    if (!orderNumber) return alert('No order to cancel');

    const payload = {
      order_number: orderNumber,
      total_amount: calculateTotal(),
      status: 'cancelled',
      items: cart,
    };

    try {
      const response = await fetch(API_ORDERS_URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });

      if (!response.ok) throw new Error('Failed to cancel order');
      setOrderStatus('cancelled');
      setCart([]);
      setOrderNumber(null);
      alert('Order cancelled!');
    } catch (error) {
      console.error('Error cancelling order:', error);
      alert(`Error: ${error.message}`);
    }
  };

  if (loading) return <div className="p-4">Loading products...</div>;
  if (error) return <div className="p-4 text-red-500">Error: {error}</div>;

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-8">E-Commerce Store</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div>
          <h2 className="text-2xl font-semibold mb-4">Products</h2>
          <ProductList products={products} addToCart={addToCart} />
        </div>
        <div>
          <h2 className="text-2xl font-semibold mb-4">Cart</h2>
          <Cart
            cart={cart}
            updateCartItem={updateCartItem}
            total={calculateTotal()}
            orderStatus={orderStatus}
            createOrder={createOrder}
            checkout={checkout}
            cancelOrder={cancelOrder}
          />
        </div>
      </div>
    </div>
  );
}
